header {
  z-index: 1000;
  background: $danger;
  box-shadow: 0 3px 15px 0 rgba(0,0,0,.65);
  .main-menu {
    .menu {
      // overflow-x: scroll;
      // overflow-y: visible;
      .profile-info {

      }
      li {
        min-width: 20%;
        a {
          display: block;
          color: white;
          text-decoration: none;
          text-transform: capitalize;
          width: 100%;
          min-height: 30px;
          line-height: 30px;
          text-align: center;
          background: rgba(0,0,0,.65);
          transition: all ease .25s;
          
          &:hover {
            color: yellow;
            background: rgba(0,0,0,.85);
          }
        }
      }

      .box {
        &-circle {
          width: 30px;
          height: 30px;
          min-width: 0;
          img {
            max-width: 100%;
          }
        }

        &.profile-info {
        }
      }
    }
  }
}
