.alert {
  padding: .5em 1em;
}
.alert.alert-success {
  background: lightgreen;
}
.alert.alert-danger {
  background: lightcoral;
}
.alert.alert-warning {
  background: lightyellow;
}
.alert.alert-info {
  background: lightblue;
}
.alert.alert-default {
  background: gainsboro;
}
.alert.alert-primary {
  background: lightskyblue;
}

.alert .alert-title {
  position: relative;
}

.alert.alert-close {
  position: relative;
}

.alert .alert-content {
  
}

.alert .alert-close-btn {
  position: absolute;
  right: .5em;
  top: .375em;
  padding: .125em .45em;
}
