@import './border-radius';
@import './box-shadow';




$primary: #29b6f6;
$danger: deeppink;
$warning: rgb(255, 221, 0);
$success: #66bb6a;
$dark: #050a05;
$gray: #2f322f;

$black: #000;
$white: #fff;

$blackNum: 0;
$whiteNum: 255;

$l1: .25;
$l2: .5;
$l3: .75;

$d1: 0.9375em;
$nM1: 0.9375em;

$p-xy-1: .25em;
$p-xy-2: .5em;
$p-xy-3: .75em;

$p-x1y2-1: .25em .5em;
$p-x1y2-2: .5em .75em;
$p-x1y2-3: .75em 1em;

$t1: .15s;
$t2: .5s;
$t3: .75s;

$ease: ease;
$easeIn: ease-in;
$easeOut: ease-out;
$cubic1: cubic-bezier(1,.6,1,1.6);
$cubic2: cubic-bezier(0.5, -0.5, 0.5, 1.5);
$cubic3: cubic-bezier(0.47, 0, 0.745, 0.715);

$transi-ease-1: all $ease $t1;
$transi-ease-2: all $ease $t2;
$transi-ease-3: all $ease $t3;

$transi-easeIn-1: all $easeIn $t1;
$transi-easeIn-2: all $easeIn $t2;
$transi-easeIn-3: all $easeIn $t3;

$transi-easeOut-1: all $easeOut $t1;
$transi-easeOut-2: all $easeOut $t2;
$transi-easeOut-3: all $easeOut $t3;

$transi-cubic1-1: all $cubic1 $t1;
$transi-cubic1-2: all $cubic1 $t2;
$transi-cubic1-3: all $cubic1 $t3;

$transi-cubic2-1: all $cubic2 $t1;
$transi-cubic2-2: all $cubic2 $t2;
$transi-cubic2-3: all $cubic2 $t3;

$border-radius-1: 0.5em;
$border-radius-2: 1em;
$border-radius-3: 1.5em;
$border-radius-4: 2em;

// 

$tran-black-1: rgba($blackNum,$blackNum,$blackNum,$l1);
$tran-black-2: rgba($blackNum,$blackNum,$blackNum,$l2);
$tran-black-3: rgba($blackNum,$blackNum,$blackNum,$l3);

$tran-white-1: rgba($whiteNum,$whiteNum,$whiteNum,$l1);
$tran-white-2: rgba($whiteNum,$whiteNum,$whiteNum,$l2);
$tran-white-3: rgba($whiteNum,$whiteNum,$whiteNum,$l3);

// 

$box-1: 0 3px 15px 0 $tran-black-1;
$box-2: 0 3px 15px 0 $tran-black-2;
$box-3: 0 3px 15px 0 $tran-black-3;


// settings:
$cuLevel: $l1;
$cuMainColor: $danger;
$cuTiming: $t3;
$cuTransition: $transi-cubic2-3;

// generals:
$cuBorderRadius: $border-radius-1;
$cuPadding: $border-radius-1;
$cuBoxShadow: $box-2;

$cuSpace: 1em;

$cuMarginTop: $cuSpace;
$cuMarginRight: $cuSpace;
$cuMarginBottom: $cuSpace;
$cuMarginLeft: $cuSpace;

$cuContainerPadding: $d1;
$cuNegativeMargin: $nM1;
$cuColPadding: $d1;

$cuSectionPadding: 2.5em;
$cuSectionMargin: 0 0 2.5em;

$cuItemPadding: 1em;
$cuItemMargin: 0 0 2.5em;


$cuInprocessCount: infinite;
$cuInprocessBg: $warning;
// $cuInprocessTiming: $cubic3;
$cuInprocessTiming: $t3;

$colors: (primary $primary) (danger $danger) (warning $warning) (success $success) (dark $dark) (gray $gray) (black $black) (white $white);
