.loading {
  position: relative;
  width: 20px;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  margin: 0 auto;
  background: radial-gradient(orange, black, rgb(255, 255, 255));

  &.negative-pos {
    top: .3em;
  }

  span {
    // $a: 10;
    // $d: 'awesome';
    // $x: "ảo giác #{$d}!";

    // &:before {
    //   content: $x;
    //   letter-spacing: 2.5px;
    // }

    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    transition: all ease .25s;
    
    @for $i from 1 through 10 {
      &.item-#{$i} {
        width: #{100% - ($i * 10%)};
        height: #{100% - ($i * 10%)};
        animation: rotatexy #{($i + random(10)) + 's'} infinite;
        background: linear-gradient(rgb(255, 191, 0), rgb(0, 0, 0), rgb(176, 0, 0))
      }
    }
  }
  .icon {
    .outer {
      .inner {
        .center {
        }
      }
    }
  }
}
