@keyframes is-posting-task {
  from {
    background: $danger;
    opacity: .75;
  }
  to {
    background: $warning;
    opacity: 1;
  }
}

@keyframes task-processing {
  from {
    filter: blur(0);
    opacity: .75;
  }
  to {
    filter: blur(10px);
    opacity: 1;
  }
}

@keyframes in-process {
  from {
    background: $warning;
    transform: rotate(0) scale(0.75);
    opacity: .75;
  }
  to {
    background: $danger;
    transform: rotate(360deg) scale(1);
    opacity: .65;
  }
}

@keyframes rotatexy {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
