.wrap-modal {
  position: fixed;
  width: 100%;height: 100%; 
  top: 0; left: 0; right: 0; bottom: 0;
  background: rgba(0,0,0,.75);
  transition: all ease .45s;
  opacity: 0;
  visibility: hidden;
  z-index: 0;
}

.wrap-modal.show {
  opacity: 1;
  visibility: visible;
  z-index: 100;
}

.wrap-modal .mask-blur {
  position: absolute;
  top: 0; right: 0;
  bottom: 0; left: 0;
  width: 100%; height: 100%;
  animation-name: show-slow;
  animation-duration: 1s;
  animation-delay: .45s;
  animation-direction: alternate;
  animation-iteration-count: infinite;
}

@keyframes show-slow {
  from {
    opacity: 0;
    background: rgba(100, 200, 200, .015);
  }
  to {
    opacity: 1;
    background: rgba(125, 255, 160, .45);
  }
}

@keyframes show-slow {
  from {
    opacity: 0;
    background: rgba(100, 200, 200, .015);
  }
  to {
    opacity: 1;
    background: rgba(125, 255, 160, .45);
  }
}

.modal {
  position: fixed;
  top: 50%; left: 50%;
  transform: translate(-50%, -50%);
  width: 90%;
  overflow: auto; 
  background: salmon;
  color: white;
  border-radius: .3125em;
}

.modal-title, .modal-body, .modal-footer {
  padding: 1em;
}

.modal-heading {
  border-bottom: 0.3125em solid white;
}

.modal-title {
  font-weight: bold;
  text-align: center;
}

