body {
  margin: 0;
  padding: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif; */
  
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.container {
  max-width: 37.6em;
  margin: 0 auto;
  padding-left: 1em;
  padding-right: 1em;
}

ul {
  padding: 0;
  list-style: none;
}

li {
  /* display: flex; */
  /* justify-content: space-between; */
  /* align-items: center; */
  padding-top: 1em;
  padding-bottom: 1em;
  /* border-bottom: 0.0625em solid seashell; */
  /* color: white; */
}

li:last-child {
  /* border-bottom: none; */
}

button, .form-input {
  border-radius: 0.1875em;
  border: none;
  padding: .5em .75em;
}

.form-input.long {
  width: 6.25em;
}
.form-input.medium {
  width: 4.375em;
}
.form-input.short {
  width: 2.5em;
}

.img {
  max-width: 100%;
}

button, input {
  outline: none;
}

button {
  cursor: pointer
}

.split-white {
  position: relative;
  top: 5px;
  width: 2px;
  height: 20px;
  margin: 0 5px;
  background: orange;
}
