@import './../../styles/mixins/render.scss';

.dropdown {
  position: absolute;
  top: 100%; left: 0;
  opacity: 0;
  visibility: hidden;
  z-index: 0;
  transition: all ease .45s;
  box-shadow: 0 .5em 2.5em 0 rgba(0,0,0,.35);
}
.dropdown .dropdown-item {
  padding: .5em 1em;
  cursor: pointer;
}
.dropdown .dropdown-item:hover {
  color: white;
  background: #333;
}
.dropdown.show {
  opacity: 1;
  visibility: visible;
  z-index: 1000;
}

@include property-bg-render(dropdown);

.dropdown .dropdown-title {
  position: relative;
}

.dropdown.dropdown-close {
  position: relative;
}

.dropdown .dropdown-content {
  
}

.dropdown .dropdown-close {
  position: absolute;
  right: .5em;
  top: .375em;
}
