.is-posting-task {
  animation-name: is-posting-task;
  @extend %animation-loop;
}

.task {
  color: orangered;
  transition: $transi-cubic2-1;

  &-remove-item {
    transform: translateX(-100%) scaleY(0);
  }
  
  &-done {
    p {
      color: yellowgreen;
      text-decoration: line-through;
    }
  }
  
  &-processing {
    animation: task-processing .4s;
  }

  .btn {
    padding: 0;
    width: 20px;
    height: 20px;
    line-height: 20px;
  }
}
