@import './mixins/render';

@include property-bg-render(btn);

.btn {
  display: inline-block;
  cursor: pointer;
  padding: $p-xy-1;

  &-style {
    border-radius: $cuBorderRadius;
    box-shadow: $cuBoxShadow;
  }
  
  &-circle {
    width: 40px;
    height: 40px;
    line-height: 40px;
    border-radius: 50%;
    text-align: center;
    box-shadow: $cuBoxShadow;
  }

  img {
    transition: $transi-cubic2-1;
  }
  
  &:hover {
    img {
      transform: scale(.75);
    }
  }

  &.center-icon {
    @extend %flex-center-xy !optional;
    img {
      @extend %align-seft-center !optional;
      height: 80%;
    }
  }

  &-sm {
    padding: .35em .35em;
  }
  &-md {
    padding: .45em .45em;
  }
  &-lg {
    padding: .55em .55em;
  }
  &-xl {
    padding: .65em .65em;
  }
}
