@import './templates/_position';

.fixed { @extend %fixed !optional; }
.fixed-top {
  @extend %fixed !optional;
  top: 0; left: 0;
}
.fixed-bottom {
  @extend %fixed !optional;
  bottom: 0; left: 0;
}
.center-xy { @extend %center-xy !optional; }
.absolute { @extend %absolute !optional; }
.relative { @extend %relative !optional; }
