@charset "UTF-8";
.box-fixed {
  position: fixed; }

.box-mask {
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.25); }

.box-flex-center-xy, .box-flex {
  display: flex; }

/* flex-flow: <‘flex-direction’> || <‘flex-wrap’> */
.box-flex-center-xy {
  justify-content: center; }

.box-flex-center-xy {
  align-content: center; }

/* item */
.box .child-center {
  align-self: center; }
